  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: cover;
}
h1 {
  font-size: 30px;
  font-weight: bold;
}
h2 {
  font-size: 24px;
  font-weight: bold;
}
.temperature {
  display: flex;
  justify-content: center;
  margin: 20px auto;
  font-size: 24px;
}
.city-name {
  /*display: flex;
  align-self: center;
  justify-content: center;*/
  margin: 5px auto;
  font-size: 24px;
}

.city-temp {
  
  margin: 5px auto;
  font-size: 24px;
}
