  font-size: 24px;
  color: white;
  background-color: transparent;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
  /*justify-content: center;*/
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  background-position: left;
}
.loclal {
  display: block;
  margin-top: 20px;
  text-align: bottom;
  background-color: beige;
}

#weather-box {
  background-color: transparent;
}
.list {
  width: 30%;
  margin: 0 auto;
  /* border-style: none;*/
}
#map-container {
  margin-bottom: 2%;
}
